.swiper {
    width: 100%;
    height: 100%;
  }
  
  .swiper-slide {
    width: 100%;
    height:768px;
    text-align: center;
    font-size: 18px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .swiper-slide > p {
    position:absolute;
    top: 220px;
    left:150px;
    color:#000;
    font-size:66px;
    float:left;
  }

  .swiper-slide > .text_box > p {
    position:absolute;
    left:10px;
    font-size:20px;
    float:left;
  }


  .swiper-slide > p > span {
    float: left;
  }

  .white {
    color: #fff;
  }

  .swiper-slide > p > b {
    left: 5px;
    position: relative;
    bottom: 23px;
    float: left;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .swiper-horizontal>.swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction 
  {
    top:600px;
    left:-655px
  }

  .swiper-pagination-bullet-active
  {
    background-color: #fff;
  }

  .swiper-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet
  {
    width: 12px;
    height: 12px;
    margin: 0 7px;
  }

  .swiper-button-prev::after
  {
      color:#fff;
  }

  .swiper-button-next::after
  {
      color:#fff;
  }
