#SNS
{
    text-align: center;
}

.sn
{
    width: 1275px;
    height: auto;
    padding: 0;
    margin:0 auto;
    display: flex;
    flex-flow: wrap row;
    justify-content: space-between;
}

.sn > li
{
    width: 300px;
    height: 315px;
    margin-bottom: 23px;
}

.sn > li > img
{
    width: 100%;
    height: 100%;
}
