.footer_bar
{
    margin-top: 130px;
    width: 100%;
    height: 50px;
    background-color: #2c2c2c;
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer_bar > div
{
    color: #fff;
    font-weight: bold;
}

.footer_bar > div::before
{
    width: 26px;
    height: 26px;
    margin-right: 5px;
    background: url('../img/ico_free.png') no-repeat;
    background-size: 100% auto;
    display: inline-block;
    content: "";
    vertical-align: -7px;
}

.footer
{
    width: 1720px;
    height: 280px;
    margin:0 auto;
}

.iconbox 
{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 25px;
}

.iconbox > ul > p
{
    line-height: 17px;
    font-size: 13px;
    color: #999;
}

.footer > .iconbox > ul > li
{
    float: left;
    padding: 6px;
    font-weight: bold;
}

.footer > .iconbox > ul > li:nth-of-type(1)
{
    padding-left: 0;
}

.footer > .iconbox > ul > li::after
{
    content: '|';
padding-left: 15px;
}

.footer > .iconbox > ul > li:nth-of-type(6):after
{
    content: '';
}

.icon 
{
    width: 306px;
    height: 169px;
}

.icon > div > li 
{
    width: 34px;
    height: 34px;
    background: url(Footer.tsx) no-repeat;
    text-indent: -9999px;
    overflow: hidden;
    display: block;
    float: right;
}

.icon > span:nth-of-type(1)
{
    line-height: 16px;
    font-size: 24px;
    margin: 10px 0;
}


.icon > span
{
    float: right;
    color: #000;
    line-height: 16px;
    text-align: right;
    font-size: 13px;
}

.icon > div > li:nth-of-type(3)
{
    background-position: -80px 0px;
}

.icon > div > li:nth-of-type(4)
{
    background-position: -120px 0px;
}

.footerlast
{
    width: 100%;
    border-top: 1px solid #ddd;
}
.w
{
    display: flex;
    justify-content: space-between;
}

.w > ul >li
{
    float: left;
    position: relative;
    padding: 6px 4px;
}

.w > ul:nth-of-type(1) > li
{
    color: #4b4b4b;
    font-size: 13px;
    font-weight: bold;
}

.w > ul:nth-of-type(1) > li::after
{
    content: '|';
    margin-left: 8px;
}

.w > ul:nth-of-type(1) > li:nth-of-type(3):after
{
    content: '';
}

.w > ul:nth-of-type(2) > li:nth-of-type(3)
{
    margin-left: 30px;
}

.w > ul:nth-of-type(2) > li:nth-of-type(3)::before
{
    position: absolute;
    left: -30px;
    top: 8px;
    content: "";
    display: block;
    width: 30px;
    height: 21px;
    background: url(../img/korea_icon.png);
}
