  }


  
.maina
{
    width: 100%;
    height: auto;
    padding: 80px 0;
    background-color: #f8f8f8;
}

.mainSliderWrap
{
    width: 304px;
    height: 428px;
    display: flex;
    flex-flow: column wrap;
    float: left;
    margin-right: 0px;
}

.imgbox
{
    width: 305px;
    height: 303px;
    position: relative;
}

.imgbox > .numberbox
{
    position: absolute;
    left: 0;
    top: 0;
    line-height: 30px;
    width: 30px;
    height: 35px;
    background: url(../img/numberbox.png) no-repeat;
    text-align: center;
    font-size: 18px;
    color: #fff;
    font-weight: normal;
    z-index: 11;
}

.imgbox > img
{
    width: 100%;
    height: 100%;
    position: absolute;
}

.imgbox > img:nth-of-type(1)
{
    z-index: 10;
    opacity: 1;
    transition: 0.4s;
    background-color: #f8f8f8;
}

.imgbox > img:nth-of-type(1):hover
{
    opacity: 0;
    transition: 0.4s;
}

.maintext_box
{
    width: 303px;
}

.maintext_box > p
{
    margin: 20px 0;
    height: 37px;
    color: #000000;
    font-weight: bold;
    display: flex;
    width: 100%;
}

.maintext_box > span
{

    font-size: 15px;
    color: #666;
    font-weight: 600;
    float: left;
}
