ul,li,ol 
{
  list-style: none;
}

.App {
  height: auto;
  width: auto;
}

.top {
  width: 100%;
  background-color: #000;
  height: 50px;
  text-align: center;
}

.top > h4{
  color: #fff;
  line-height: 50px;
  font-size: 16px;
}

.top > h4 > span{
  color: #77FFEf;
}

.navbar {
  padding-top: 0;
  padding-bottom: 0;
}

.navbar-expand-lg .navbar-nav .nav-link {
    padding-right: 0rem;
    padding-left: 0rem;
    margin: 0 20px;
    font-size: 17px;
  color: #000;
  font-weight: 620;
  border-bottom: 2px solid #fff;
  height: 37px;
}

.navbar-expand-lg .navbar-nav .nav-link:hover {
border-bottom: 2px solid #000;
color: #000;
}

.navbar-expand-lg .navbar-nav:nth-of-type(2) .nav-link:hover {
  border-bottom: 2px solid #ffffff;
  }

.swiper {
  width: 100%;
  height: 330px;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper {
  margin-left: auto;
  margin-right: auto;
}

.main {
  color: #767676;
}

.wpqkf > .nav-link > img
{
  width: 20px;
  height: 20px;
}
