.slick-initialized
{
  width: 1275px;
  margin-top: 30px;
}

.New_Arrivals
{
  margin-top: 80px;
}

.first_slider 
{
  width: 1275px;
  margin:0 auto;
}

.slick-initialized > button:nth-of-type(1)
{
  position: absolute;
  left: -95px;
  z-index: 10;
  top: 43%;
}

.slick-initialized > button:nth-of-type(1)::before
{
  content: '<';
  color: #000;
  opacity: .25;
    font-size: 60px;
}

.slick-initialized > button:nth-of-type(2)
{
  position: absolute;
  right: -63px;
  z-index: 10;
  top: 43%;
}

.slick-initialized > button:nth-of-type(2)::before
{
    content: '>';
    color: #000;
    opacity: .25;
    font-size: 60px
}

.Arrivals_box
{
    width: 305px;
    height: 502px;
}

.Arrivals_box > img:nth-of-type(1)
{
    position: absolute;
    z-index: 10;
    width: 305px;
    height: 80%;
    transition: 0.3s;
}

.Arrivals_box > img:nth-of-type(1):hover
{
    opacity: 0;
}

.Arrivals_box > img
{
    width: 305px;
    height: 80%;
}

.Arrivals_box > p
{
    margin-top: 10px;
    color: #222;
    height: 44px;
    font-weight: bold;
    font-size: 17px;
}

.Arrivals_box > span
{
    color: #858585;
    font-size: 17px;
}
