.Mlb_imgbox
{
    width: 100%;
    height: 940px;
    padding: 0;
}

.Mlb_imgbox > li
{
    width: 410px;
    height: 475px;
    background-color: antiquewhite;
    float: left;
    margin: 0 15px 15px 0px;
    position: relative;
}

.mlb_sty_textbox
{
    width: 100%;
    text-align: center;
}

.hover_bar
{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color:#42424273;
    z-index: 10;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: 0.2s;
}

.hover_bar:hover
{
    opacity: 1;
}
