.Quick
{
    margin-top: 80px;
}

.Quick > .Quick_box
{
    margin:0 auto;
    width: 1275px;
    height: 672px;
    display: flex;
    flex-flow: column ;
    justify-content: space-between;
}

.Quick > .Quick_box > .bakc
{
    display: flex;
}

.Quick > .Quick_box > .bakc > .img_sbox 
{
    overflow: hidden;
    width: 624px;
    height: 328px;
}

.Quick > .Quick_box > .bakc > .tebox
{
    overflow: hidden;
    width: 624px;
    height: 328px;
}

.Quick > .Quick_box > .bakc > .img_sbox > img
{

    transition: 0.3s;
}

.Quick > .Quick_box > .bakc > .img_sbox > img:hover
{
    transform: scale(1.1);

}
