  .swiper {
    width: 100%;
    height: 100%;
  }
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
  
    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .scroll
  {
    width: 262px;
    height: 377px;
    background-color:#fff;
    padding-bottom: 27px;
  }

  .scroll > img
  {
    width: 100%;
    height: 262px;
  }

  .scroll > img:nth-of-type(1)
  {
    position: absolute;
    transition: 0.3s;
  }

  .scroll > img:nth-of-type(1):hover
  {
    opacity: 0;
  }

  .scroll > .collecton_textbox
  {
    width: 100%;
    height: auto;
    padding: 0 4px;
  }

  .scroll > .collecton_textbox > p
  {
    margin-top: 25px;
    color:#666;
    font-size: 13px;
    text-align: left;
    font-weight: 600;
    height: 37px;
  }

  .scroll > .collecton_textbox > span
  {

    color:#666;
    font-size: 13px;
    float: left;

  }

.swiper-scrollbar-drag
{
  background: #fff;
}
  
