.C
{
    width: 1200px;
    margin:0 auto;
    height:auto;
}

.cart
{
    width: 100%;
    height: 81px;
    border-bottom: 3px solid #000;
}

.cart > h3
{
    text-align: center;
}

.section
{
    width: 100%;
    height: auto;
}

.section > .dnl
{
    width: 100%;
    height: 70px;
    background-color: #000;
    margin: 30px 0 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.section > .dnl > li:nth-of-type(1)
{
color: #fff;
}

.section > .dnl > li:nth-of-type(1)::before
{
    content: '';
}

.section > .dnl > li
{
    font-size: 20px;
    color: #555555;
    float: left;
    padding: 0 20px;
    font-weight: 600;
}

.section > .dnl > li::before
{
    content: '>';
    padding-right: 40px;
}

.asd
{
    padding: 0;
    width: 100%;
    height: 55px;
}

.asd > li
{
    width: 391px;
    height: 100%;
    float: left;
    border-bottom: 2px solid #b6b6b6;
    color: #b6b6b6;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 9px;
}

.asd > li:nth-of-type(1)
{
    border-bottom: 2px solid #000;
    color: #000;
}
