.mainS > ul
{
    width: 1275px;
    margin:0 auto;
    padding: 0;
    height: auto;
    display: flex;
    flex-flow: wrap row;
    justify-content: space-between;
    align-content: space-between;
}

.mainS > ul > li
{
    float: left;
    width: 305px;
    height: 507px;
    background-color:#f8f8f8;
    margin-top: 20px;
}

.mainS > ul > li > .pick_textbox 
{
    width: 100%;
    height: 102px;
    background-color: #fff;
    padding-top: 16px;
}

.mainS > ul > li > img
{
    width: 100%;
    height: 406px;
}

.mainS > ul > li > .pick_textbox > p
{
    text-align: left;
    color: #222;
    font-weight: bold;

}

.mainS > ul > li > .pick_textbox > span
{
    text-align: left;
    display: block;
    color: #767676;
    font-size: 18px;
}
